.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
*{
  padding: 0;
  margin: 0;
}

nav{
 
  display: flex;
  
  margin-bottom: 30px;
}
nav ul li{
font-size: 1.3rem;
list-style: none;
cursor: pointer;
}



.Btn{
  background-color: blueviolet;
  border: 2px solid black;
  border-radius: 6px;
  margin-top: 10px;
  color: #fff;
  margin-right: 30px;
}
.blue-theme{
  background-color: rgb(71, 71, 226);
 
}
